import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login/login.vue'
import charts from '@/components/charts/charts.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/charts',
      name: 'charts',
      component: charts
    }
    ,{path:'/',redirect:'/login'}
  ]
})



