<template>
  <div class="box">
            <div class="login">
                  <div class="input-box head" style="border: 0">茶码头营业报表查看系统</div>
                  <div class="input-box">
                      <p class="fl">账户:</p>
                    <input class="fl" type="text" placeholder="请输入用户名" v-model="userName">
                  </div>
                  <div class="input-box">
                    <p class="fl">密码:</p>
                    <input class="fl" type="password" placeholder="请输入密码" v-model="pwd">
                  </div>
                  <div class="btn" @click="login">登录</div>
            </div>
            <!--报错模板-->
            <v-msg  :msg="msg" :msgShow="msgShow"></v-msg>
            <!--loading-->
            <v-loading v-if="isLoading"></v-loading>
            <!--loading/end-->
  </div>
</template>
<script>
  export default{
    data(){
      return{
        isLoading: false,          //是否出现loading框
        iSsuccess:false,           //是否ajax请求完成
        msgShow: false,            //是否显示错误消息模态框
        msg: '',                   //错误消息提示内容
        userName:'',
        pwd:'',
      }
    },
    methods: {
      //登录
      login(){
        const _this=this;
        if (!/^1(3[0-9]|4[57]|5[0-35-9]|7[013678]|8[0-9])[\d]{8}$/g.test(this.userName)) {
          this.msgHint('请填写正确手机号码');
          return;
        }
        if(_this.pwd==''){
          _this.msgHint('请输入密码');
          return;
        }
        _this.ajax(_this.port.login,{phone:_this.userName,password:_this.pwd},'post',function(res){
          if(res.code==1){
            window.localStorage.setItem('login', 'success');
            window.localStorage.setItem('roledata', JSON.stringify(res.data['role']));
            window.localStorage.setItem('storeinfo', JSON.stringify(res.data['store']));
            window.localStorage.setItem('userinfo', JSON.stringify(res.data['user']));
            _this.$router.push({name:'charts'})
          }else{
            _this.msgHint(res.msg);
          }
        })
    
      }
    }
  }
</script>
<style>
  .box{
    width:100%;
    height: 100%;
    background: url("../../../static/images/timg.jpg") no-repeat ;
    background-size: 100% 100%;
    position: fixed;
    top:0!important;
    left: 0;
    top:0;
    bottom: 0;
  }
  .login{
    z-index: 999;
    width:5rem;
    height:4rem;
    position: fixed;
    top:50%;
    left: 50%;
    margin-left: -2.5rem;
    margin-top: -2rem;
    background: rgba(238,238,230,.38);
    -webkit-box-shadow: 3px 3px 3px rgba(6,6,6,.5);
    -moz-box-shadow: 3px 3px 3px rgba(6,6,6,.5);
    box-shadow: 3px 3px 3px rgba(6,6,6,.5);
  }
  .input-box{
    height: .6rem;
    line-height: .6rem;
    clear: both;
    width:4rem;
    margin: .3rem auto;
    border-bottom: 1px solid #eee;
  }
  .input-box p{
    margin-right: .2rem;
    color: #fff;
    -webkit-text-shadow: 3px 3px 3px rgba(6,6,6,.5);
    -moz-text-shadow: 3px 3px 3px rgba(6,6,6,.5);
    text-shadow: 3px 3px 3px rgba(6,6,6,.5);
  }
  .input-box input{
    border: 0;
    background: transparent;
    -webkit-text-shadow: 3px 3px 3px rgba(6,6,6,.5);
    -moz-text-shadow: 3px 3px 3px rgba(6,6,6,.5);
    text-shadow: 3px 3px 3px rgba(6,6,6,.5);
    color: #fff;
  }
  ::-webkit-input-placeholder{
    color: #fff;
  }
  ::-moz-placeholder{
    color: #fff;
  }
 .head{
   color: rgba(252,164,10,1);
   font-size: .3rem;
   text-align: center;
   -webkit-text-shadow: 3px 3px 3px rgba(6,6,6,.5);
   -moz-text-shadow: 3px 3px 3px rgba(6,6,6,.5);
   text-shadow: 3px 3px 3px rgba(6,6,6,.5);
 }
  .btn{
    width:3rem;
    height:.6rem;
    line-height: .6rem;
    color: #fff;
    padding: 0;
    background: rgba(186,144,104,.5);
    -webkit-box-shadow: 3px 3px 3px rgba(6,6,6,.5);
    -moz-box-shadow: 3px 3px 3px rgba(6,6,6,.5);
    box-shadow: 3px 3px 3px rgba(6,6,6,.5);
  }
  .btn:hover{
    color: #fff;
  }
</style>



