<script>
  $(function(){
    
  })
  
  
  export default{
    data(){
      return{
        isLoading: false,          //是否出现loading框
        iSsuccess:false,           //是否ajax请求完成
        msgShow: false,            //是否显示错误消息模态框
        msg: '',                   //错误消息提示内容
        start_time:'',         //开单时间的开始
        end_time:'',
        page:1,
        pageList:[],
        pageNumList:[],//开单时间的结束
        dataList:[],//数据
        bj:[],//包间
        sp:[],//商品
        hy:[],//会员
        yh:[],//优惠
        zfb:[],//支付宝
        wx:[],//微信
        xj:[],//现金
        pos:[],//刷卡
        cd:[],//茶豆
        sz:[],//散座
        bJ:[],//包间
        yd:[],//预定
        sum:'',//总销售额
        statementData:[],//报表数据
        time:[],//时间
        businessSum:[],//营业数据
        wx_paySum:[],//微信数据
        ali_paySum:[],//支付宝数据
        vip_paySum:[],//会员数据
        money_paySum:[],//现金数据
        beans_amountSum:[],//茶豆币数据
        preferentialSum:[],//优惠数据
        card_paySum:[],//刷卡数据
        data_List:[],//统计报表
        seniorityList:[]//排行
      }
    },
    methods: {
//            弹出框显示隐藏
      popup (id){
        var id = '#'+id;
        $(id).addClass('in');
        $('.bgblack').addClass('in');
      },
      close(id){
        var id = '#'+id;
        $(id).removeClass('in');
        $('.bgblack').removeClass('in');
      },
//          是否启动密码
      chelick(){
        $('.password').show();
      },
      chelickHide(){
        $('.password').hide();
      },
//          导航切换
      navclick(id){
        var _this = this;
        var id = id;
        var bleid =  '#contable' + id;
        var nav = '#nav' + id;
        if(id==''){
          _this.dataList=[];
          _this.sz=[];
          _this.bJ=[];
          _this.yd=[];
          _this.getRealTimeData();
        }
        if(id=='2'){
          _this.statementData=[];
          _this.getStatementData();
        }
        if(id=='3'){
          _this.statementData=[];
          _this.page=1;
          _this.getCaller();
        }
        if(id=='4'){
          _this.statementData=[];
          _this.getSeniorityData();
        }
        $('.contable').hide();
        $('.bench').hide();
        $('#nav').siblings('.current').removeClass('current').end().removeClass('current');
        $(bleid).show();
        $(nav).addClass('current');
      },
      //时间插件
      selectDate(type){
        var _this = this;
        laydate({
            istoday:false,
            choose: function (dates)
          {
            if(type=='start'
            ){
              _this.start_time = dates;
            }else{
              _this.end_time = dates;
            }
          },
          isclear:false
        });
      },
      //获取当前时间
      getDate(){
        var startTime='';
        var endTime='';
        var myDate = new Date();
        var Y = myDate.getFullYear();
        var M = myDate.getMonth() + 1;
        startTime+=Y+'-';
        endTime+=Y+'-';
        if(M < 10){
          startTime+='0'+M+'-';
          endTime+='0'+M+'-';
        }else{
          startTime+=M+'-';
          endTime+=M+'-';
        }
        startTime+='01';
        endTime+= this.getDates();
        this.start_time=startTime;
        this.end_time=endTime;
      },
      //一个月有多少天
      getDates(){
        var curDate = new Date();
        /* 获取当前月份 */
        var curMonth = curDate.getMonth();
        /*  生成实际的月份: 由于curMonth会比实际月份小1, 故需加1 */
        curDate.setMonth(curMonth + 1);
        /* 将日期设置为0, 这里为什么要这样设置, 我不知道原因, 这是从网上学来的 */
        curDate.setDate(0);
        /* 返回当月的天数 */
        return curDate.getDate();
      },
      //查询报表
      inquire(){
        this.getStatementData();
      },
      //查询销量
      xl(){
        this.getSeniorityData();
      },
      //查询访客
      fk(){
        this.getCaller();
      },
      //获取实时数据
      getRealTimeData(){
        var _this = this;
        _this.ajax(_this.port.realTimeData,{},'get',function(res){
          if(res.code==1){
            _this.dataList=[];
            _this.sz=[];
            _this.bJ=[];
            _this.yd=[];
            _this.dataList = res.data.list;
            _this.bj=_this.dataList.bjsr;
            _this.sp=_this.dataList.goods_sr;
            _this.hy=_this.dataList.vip_pay;
            _this.yh=_this.dataList.preferential;
            _this.zfb=_this.dataList.ali_pay;
            _this.wx=_this.dataList.wx_pay;
            _this.xj=_this.dataList.money_pay;
            _this.pos=_this.dataList.card_pay;
            _this.cd=_this.dataList.beans_amount;
            _this.sz=res.data.szl.sz;
            _this.bJ=res.data.szl.bj;
            _this.yd=res.data.szl.yd;
          }else{
            _this.msgHint(res.msg);
          }
        })
      },
      //获取报表实时数据
      getStatementData(){
        var _this = this;
        _this.ajax(_this.port.statementData,{start_time:_this.start_time,end_time:_this.end_time},'get',function(res){
          if(res.code==1){
            _this.statementData=[];
            _this.statementData=res.data.list;
            _this.bj=_this.statementData.bjsr;
            _this.sp=_this.statementData.goods_sr;
            _this.hy=_this.statementData.vip_pay;
            _this.yh=_this.statementData.preferential;
            _this.zfb=_this.statementData.ali_pay;
            _this.wx=_this.statementData.wx_pay;
            _this.xj=_this.statementData.money_pay;
            _this.pos=_this.statementData.card_pay;
            _this.cd=_this.statementData.beans_amount;
            _this.time=res.data.time;
            _this.businessSum=res.data.sum;
            _this.wx_paySum=res.data.wx_pay;//微信数据
            _this.ali_paySum=res.data.ali_pay;//支付宝数据
            _this.vip_paySum=res.data.vip_pay;//会员数据
            _this.money_paySum=res.data.money_pay;//现金数据
            _this.beans_amountSum=res.data.beans_amount;//茶豆币数据
            _this.preferentialSum=res.data.preferential;//优惠数据
            _this.card_paySum=res.data.card_pay;//刷卡数据
            _this.data_List=res.data.data_list;//统计报表
            console.log(_this.dataList)
          }else{
            _this.msgHint(res.msg);
          }
        })
      },
      //获取排行数据
      getSeniorityData(){
        var _this = this;
        _this.ajax(_this.port.SeniorityData,{start_time:_this.start_time,end_time:_this.end_time},'get',function(res){
          if(res.code==1){
            _this.seniorityList=[];
            _this.seniorityList=res.data;
          }else{
            _this.msgHint(res.msg);
          }
        })
      },
      //获取访客数据
      getCaller(){
        var _this = this;
        _this.ajax(_this.port.caller,{start_time:_this.start_time,end_time:_this.end_time,page:_this.page},'get',function(res){
          if(res.code==1){
            _this.seniorityList=[];
            _this.pageNumList=[];
            _this.seniorityList=res.data.list;
            _this.pageNumList =res.data;
            _this.getpage(_this.page);
            console.log(res)
          }else{
            _this.msgHint(res.msg);
          }
        })
      },
      //分页
      changePage(page,type){
        if(Number(page)){
          var _this=this;
          if(type) {
            if (type == 'prev') {
              if (page > 1) {
                page--;
                _this.page = page;
                _this.getpage(page);
                _this.getCaller();
              } else {
                return;
              }
            } else {
              if (page < _this.pageNumList.pageNum) {
                page++;
                _this.page = page;
                _this.getpage(page);
                _this.getCaller();
              } else {
                return;
              }
            }
          }else{
            _this.page=page;
            _this.getpage(page);
            _this.getCaller();
          }
        }
      },
      getpage(page){
        var _this=this;
        if(_this.pageNumList.pageNum>10){
          //当前页小于6
          if(page<6){
            _this.pageList=[];
            for(let i=1;i<=7;i++){
              if(i==7){
                _this.pageList.push(0);
              }else{
                _this.pageList.push(i);
              }
            }
            _this.pageList.push(_this.pageNumList.pageNum);
            _this.page=page;
          }
          //当前页中间部分
          if(page>5 && page<_this.pageNumList.pageNum-2){
            _this.pageList=[];
            _this.pageList.push(1,0);
            for(let i=page-2;i<=page+2;i++){
              _this.pageList.push(i);
            }
            _this.pageList.push(0,_this.pageNumList.pageNum);
            _this.page=page;
          }
          //最后几页
          if(_this.pageNumList.pageNum-2<=page && page<=_this.pageNumList.pageNum){
            _this.pageList=[];
            _this.pageList.push(1,0);
            for(let i=_this.pageNumList.pageNum-6;i<=_this.pageNumList.pageNum;i++){
              _this.pageList.push(i);
            }
            _this.page=page;
          }
        }else{
          _this.page=page;
          _this.pageList=[];
          for(let i=1;i<=_this.pageNumList.pageNum;i++){
            _this.pageList.push(i);
          }
        }
      }
    },
    mounted(){
      var _this=this;
      $('.money').click(function(){
        $(this).addClass('active').siblings('.money').removeClass('active');
        
      });
      this.getRealTimeData();
      this.getDate();
      //进度条颜色配置
      $('.yuding .el-progress-bar__innerText').css({'background':'#e6ad74'});
      $('.baojian .el-progress-bar__innerText').css({'background':'#13ce66'});
      $('.sanzuo .el-progress-bar__innerText').css({'background':'#20a0ff'});
      $('.el-progress-bar__inner').css({'transition':'all .5s'});
    },
    //组件更新后
    updated(){
      //折线图显示与配置
      var chartsWidth = 600;
      $('#charts').css({'width':chartsWidth+'px'});
      var myChart = echarts.init(document.getElementById('charts'));
      $('.el-progress-circle').css({'margin':'0 auto'});
      $('.el-progress__text').css({
       'top' : '30%',
      'position': 'absolute',
      'width': '100%',
      'text-align': 'center',
        'padding': '0 30px 0 0',
      });
      $('.el-progress-bar__outer').css({
        'border-radius' : '.2rem',
        'background': '#e4e8f1',
        'overflow': 'hidden',
        'position': 'relative',
        'vertical-align': 'middle',
        'color': '#fff',
      });
      var option = {
        title: {
          text: '总营业额走势图',
          subtext: '(向右滑动查看)'
        },
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data:['姚大侠','大侠飞']
        },
        color:
          ['#c23531','rgba(222,222,222,0)','rgba(222,222,222,0)','rgba(222,222,222,0)', 'rgba(222,222,222,0)','rgba(222,222,222,0)',  'rgba(222,222,222,0)', 'rgba(222,222,222,0)','rgba(222,222,222,0)', 'rgba(222,222,222,0)', 'rgba(222,222,222,0)']
        ,
        toolbox: {
          show: true,
          feature: {
            dataZoom: {
              yAxisIndex: 'none'
            },
            magicType: {type: ['line', 'bar']},
            restore: {},
            saveAsImage: {},
          }
        },
        xAxis:  {
          type: 'category',
          boundaryGap: false,
          data: this.time
        },
        yAxis: {
          type: 'value',
          axisLabel: {
            formatter: '{value}'
          }
        },
        series: [
          {
            name:'总营业收入',
            type:'line',
            data:this.businessSum,
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ],
              symbolSize:[100,50]
              
            },
            markLine: {
              data: [
                {type: 'average', name: '平均值'}
              ]
            }
          },
          {
            name:'微信总收入',
            type:'line',
            data:this.wx_paySum,
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ]
            }
          },
          {
            name:'支付宝总收入',
            type:'line',
            data:this.ali_paySum,
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ]
            }
          },
          {
            name:'会员消费总收入',
            type:'line',
            data:this.vip_paySum,
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ]
            }
          },
          {
            name:'现金总收入',
            type:'line',
            data:this.money_paySum,
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ]
            }
          },
          {
            name:'茶豆币总收入',
            type:'line',
            data:this.beans_amountSum,
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ]
            }
          },
          {
            name:'总优惠',
            type:'line',
            data:this.preferentialSum,
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ]
            }
          },
          {
            name:'pos刷卡总收入',
            type:'line',
            data:this.card_paySum,
            markPoint: {
              data: [
                {type: 'max', name: '最大值'},
                {type: 'min', name: '最小值'}
              ]
            }
          }
        ]
      };

      myChart.setOption(option);
      
    }
  }

</script>
<style scoped>
  .contable{
    display: none;
  }
 
  .bench{
    display:none;
    top:50px;
  }
  .el-progress--circle {
    top:42%;
  }
  
  .el-progress{
    
  }
  .el-progress-circle{
    margin:0 auto!important;
  }
  tr{
    height:38px;
    border: 1px solid #ebe8e4
  }
  td{
    border: 1px solid #ebe8e4
  }
.redcolor{
  color: #e64c2e;
  font-size: .38rem;
  font-weight: bold;
  display: block;
  margin-top: .2rem;
}
.reports_rate{
  margin:0 auto;
}
.margin-bottom{
    margin-bottom: .5rem;
  }

  .f-3rem{
    font-size: .3rem;
  }
  .hasDatepicker{
    width:2rem;
    margin:  0 .1rem;
  }
  .linklist a{
    color: #777;
    text-decoration: none;
    padding: 5px 8px;
    margin: 0px 3px;
    transition: all 0.2s;
  }
  .linklist a.current{
    color: #fff;
    text-decoration: none;
    background: rgb(32, 160, 255);
    border-radius: 3px;
    transition: all 0.2s;
  }
  .padding_top{
    padding: 10px 0 0 0;
  }
  .nav{
    position: fixed;
    bottom:0;
    height: .68rem;
    line-height: .68rem;
    background: #fff;
    width:100%;
    z-index: 999;
    border-top:1px solid #eee;
  }
  .nav a{
    display: block;
    color: #999;
    text-decoration: none;
  }
 .nav a.current{
   display: block;
   color: #fff;
   background: rgb(32, 160, 255);
 }
  .contable_title{
    border-bottom: 1px solid #eee;
  }
  .search{
    display: inline-block;
    background: rgb(32, 160, 255);
    color: #fff;
    width:1rem;
    height: .4rem;
    line-height: .4rem;
    border-radius: .05rem;
  }
  h2{
    overflow: hidden;
  }
  input{
    height: .4rem;
    border-radius: .05rem;
    border: 1px solid #eee;
    text-align: center;
  }
  .jymxtime{
    line-height: .4rem;
  }
</style>



