export  default {
  install: function (Vue) {
    Vue.prototype.port = {
      'realTimeData':'statement/actual-data',//实时数据
      'statementData':'statement/operating-data',//营业报表数据
      'SeniorityData':'statement/goods',//营业报表数据
      'caller':'statement/wx-user',//访客记录
      'login':'login/index',//登录
    };
    Vue.prototype.hostUrl = 'http://192.168.2.222:8081/';
    Vue.prototype.ajax = function (url, data, methodType, callback,is_loading) {
      var _this = this;
      //const  HostUrl=
      //  const  HostUrl='http://192.168.2.174/index.php/';
      if(is_loading==false){
      }else{
        this.isLoading = true;
        this.iSsuccess = true;
      }
      $.ajax({
        type: methodType,
        url: _this.hostUrl + url,
        data: data,
        timeout: 15000,
        dataType: 'json',
        success: function (data) {
          if(is_loading==false){
          }else{
            _this.isLoading = false;
            _this.iSsuccess = false;
          }
          callback(data);
        },
        error: function (XMLHttpRequest, textStatus, errorThrown) {
          console.log(textStatus)
          if (textStatus == 'timeout') {
            //alert('网络超时');
              _this.isLoading = false;
              _this.iSsuccess = false;
          } else if (textStatus == 'error') {
            //alert('网络错误');
              _this.isLoading = false;
              _this.iSsuccess = false;
          }
          else if (textStatus == 'parsererror') {
            //alert('数据异常');
              _this.isLoading = false;
              _this.iSsuccess = false;
          }
        }
      });
    };
    //信息提示
    Vue.prototype.msgHint = function (str) {
      var _this = this;
      this.msgShow = true;
      this.msg = str;
      setTimeout(function () {
        _this.msgShow = false;
      }, 2000)
    };
    Vue.prototype.getRole = function () {
      return window.localStorage.getItem('roledata');
    };
		//获取权限存入本地
    (function () {
      const HostUrl = 'http://192.168.2.222:8081/';
      $.ajax({
        type: 'get',
        url: HostUrl + 'role/get-roles',
        data: '',
        dataType: 'json',
        success: function (data) {
          console.log(data.data)
          window.localStorage.setItem('roledata', JSON.stringify(data.data));
        },
        error: function (err) {
          console.log(err);
        }
      });
    })()


  }
}






















